.icon {
  display: inline-block;
  width: 1em;
  height: 1em;
  fill: currentColor;
  stroke: currentColor;
}

.icon-lnr-map-marker {
  font-size: 2rem;
  width: 1em;
}

.icon-lnr-phone-handset {
  font-size: 2rem;
  width: 1em;
}

body, div, dl, dt, dd, ul, li, h1, h2, h3, h4, h5, h6, pre, code, form, fieldset, input, textarea, p, blockquote, th, td {
  margin: 0;
  padding: 0;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

fieldset, img, abbr {
  border: 0;
}

address, caption, cite, code, dfn, em, strong, th, var {
  font-style: normal;
  font-weight: normal;
}

ul li {
  list-style: none;
}

caption, th {
  text-align: left;
}

h1, h2, h3, h4, h5, h6 {
  font-size: 100%;
  font-weight: normal;
}

sup {
  vertical-align: text-top;
}

sub {
  vertical-align: text-bottom;
}

input, textarea, select {
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
}

legend {
  color: #000000;
}

article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section, main {
  display: block;
}

img {
  max-width: 100%;
  height: auto;
}

html, body, .page {
  height: 100%;
}

html {
  font-size: 62.5%;
}

body {
  font-family: "Roboto", "Calibri", "Verdana", sans-serif;
  font-size: 1.6rem;
  min-width: 32rem;
  line-height: 1.2;
  overflow-x: hidden;
}

body.popup-open {
  overflow: hidden;
}

a {
  text-decoration: none;
  color: inherit;
}

button {
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
  background-color: transparent;
  border: none;
}

img {
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

.page {
  display: flex;
  flex-direction: column;
}

.container {
  flex-grow: 1;
}

.wrapper, .full {
  max-width: 1600px;
  margin: 0 auto;
  padding-left: 30px;
  padding-right: 30px;
  height: 100%;
  position: relative;
}

.full {
  max-width: 192rem;
}

.no-event {
  pointer-events: none;
  cursor: no-drop;
  opacity: 0.5;
}

.no-scroll {
  overflow: -moz-scrollbars-none;
}

.no-scroll::-webkit-scrollbar {
  width: 0;
}

.hidden {
  opacity: 0;
  visibility: hidden;
}

#svg-sprite {
  position: absolute;
  width: 0;
  height: 0;
}

.relation {
  position: relative;
}

.relation__ratio {
  height: 0;
}

.relation__ratio--16-9 {
  padding-top: 56.25%;
}

.relation__ratio--1-1 {
  padding-top: 100%;
}

.relation__content {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  border: 0;
}

.youtube {
  position: relative;
}

.youtube__ratio {
  padding-top: 56.25%;
  height: 0;
}

.youtube__iframe {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  border: 0;
  overflow: hidden;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}

.youtube__play {
  position: absolute;
  left: 50%;
  top: 50%;
  width: 68px;
  height: 48px;
  margin-left: -34px;
  margin-top: -24px;
  cursor: pointer;
  border: 0;
  background-color: transparent;
  padding: 0;
  opacity: 0.8;
  filter: "alpha(opacity=80)";
}

.youtube__play:focus {
  outline: 0;
  opacity: 1;
  filter: "alpha(opacity=100)";
}

.youtube__play:hover .ytp-large-play-button-bg {
  transition: fill 0.1s cubic-bezier(0, 0, 0.2, 1), fill-opacity 0.1s cubic-bezier(0, 0, 0.2, 1);
  fill: #FF0000;
  fill-opacity: 1;
}

.youtube__play svg {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
}

.youtube__play .ytp-large-play-button-bg {
  transition: fill 0.1s cubic-bezier(0.4, 0, 1, 1), fill-opacity 0.1s cubic-bezier(0.4, 0, 1, 1);
  fill: #212121;
  fill-opacity: 0.8;
}

@media screen and (max-width: 768px) {
  .wrapper, .full {
    padding-left: 15px;
    padding-right: 15px;
  }
}
